export default {
  props: {
    parsedate: {
      type: Function,
      required: true
    },

    parseprice: {
      type: Function,
      required: true
    },

    expanddetail: {
      type: Function,
      required: true
    },

    expansion: {
      type: Array,
      required: true
    },

    changeqtysubsitem: {
      type: Function,
      required: true
    },

    deleterp: {
      type: Function,
      required: true
    },

    selectsubsdate: {
      type: Function,
      required: true
    },

    changehandler: {
      type: Function,
      required: true
    },

    changedeliverytime: {
      type: Function,
      required: true
    }
  },

  computed: {
    rp_order () {
      return this.$store.state.rp_order
    },

    dates () {
      return this.$store.state.dates
    },

    store () {
      return this.$store.state.store
    }
  },

  methods: {
    parse_price_per_item (price) {
      return `Rp. ${price.toLocaleString().replace(/,/, '.')}`
    }
  }
}
