import NormalOrder from '@/components/Cart/Platforms/mobile/normal'
import RPOrder from '@/components/Cart/Platforms/mobile/rp-order'
// custom setup
import Bundler from '@/components/Cart/Platforms/mobile/bundler'
// custom setup

export default {
  components: {
    NormalOrder,
    RPOrder,
    Bundler
  },

  props: {
    expanddetail: {
      type: Function,
      required: true
    },
    updatecache: {
      type: Function,
      required: true
    },
    expansion: {
      type: Array,
      required: true
    },
    subsorder: {
      type: Number,
      required: true
    },
    normalorder: {
      type: Number,
      required: true
    },
    changeqty: {
      type: Function,
      required: true
    },
    parsedate: {
      type: Function,
      required: true
    },
    parseprice: {
      type: Function,
      required: true
    },
    changeqtysubsitem: {
      type: Function,
      required: true
    },
    deleterp: {
      type: Function,
      required: true
    },
    selectsubsdate: {
      type: Function,
      required: true
    },
    changehandler: {
      type: Function,
      required: true
    },
    changenotes: {
      type: Function,
      required: true
    },
    changedeliverytime: {
      type: Function,
      required: true
    },
    changedeliverysingletime: {
      type: Function,
      required: true
    },
  },

  computed: {
    // custom setup
    customized_values() {
      return this.$store.state.customized_values
    },

    customized_values_total() {
      let total = 0

      if (this.customized_values) {
        this.customized_values.forEach(el => {
          total += el.total
        })
      }

      return total
    },
    // custom setup

    delivery_time_normal() {
      return this.$store.state.delivery_time_normal
    },

    store() {
      return this.$store.state.store
    },

    rp_order () {
      return this.$store.state.rp_order
    },

    cart () {
      return this.$store.state.cart
    },

    notes () {
      return this.$store.state.user_notes
    },

    dates () {
      return this.$store.state.dates
    }
  },
}
